/* ë¶€ëª¨ ì»¨í…Œì´ë„ˆ: ë¶€ëª¨ ìš”ì†Œ ë‚´ì—ì„œ absolute ìœ„ì¹˜ë¥¼ ìž¡ì„ ìˆ˜ ìžˆê²Œ í•¨ */
.window-container {
  position: relative;
  width: 100%;
  height: 100%;
  background-color: green;
}

/* ì°½ ê¸°ë³¸ ìŠ¤íƒ€ì¼ */
.window {
  position: absolute;
  max-width: 80%; /* ì°½ ë„ˆë¹„, í•„ìš”ì— ë”°ë¼ ì¡°ì • */
  background-color: #ddd;
  border: 2px solid black;
  box-shadow: 4px 4px black;
  background-color: #d3d3d3;
  border-top: 2px outset #777;
  border-left: 2px outset #777;
  font-family: sans-serif;
  z-index: 10;
  display: flex;
  flex-direction: column;
}

/* ì°½ í—¤ë” ìŠ¤íƒ€ì¼ */
.window-header {
  background-color: #c417fe;
  color: white;
  font-weight: bold;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 4px 8px;
  cursor: move; /* ë‚˜ì¤‘ì— ë“œëž˜ê·¸ ê°€ëŠ¥í•˜ê²Œ êµ¬í˜„í•  ê²½ìš° ìœ ìš© */
  user-select: none;
  flex-shrink: 0;
  font-size: 3vh;
}

.window-title {
  font-family: var(--font-game);
}

/* ë‹«ê¸° ë²„íŠ¼ */
.window-close {
  background-color: red;
  color: white;
  border: 1px white;
  font-weight: bold;
  cursor: pointer;
  width: 24px;
  height: 24px;
  box-shadow: 2px 2px black;
}
.window-close:active {
  transform: translateY(2px);
  box-shadow: 0 0 0 black;
}

/* ì°½ ë‚´ìš© ì˜ì—­ */
.window-body {
  padding: 12px;
  font-size: 3vh;
  color: black;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex: 1;
}

.inactive {
  pointer-events: none;
}
.inactive > .window-header {
  background-color: #939393;
}
.inactive > .window-header > .window-close {
  background-color: #757575;
}

/* Ensure the parent container of draggable windows allows proper positioning */
.main-screen__screen {
  position: relative;
}
