@keyframes fadeOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.glitch-out {
  position: relative;
  /* ë©”ì¸ ì• ë‹ˆë©”ì´ì…˜: ê¸°ì¡´ ìš”ì†Œì— ê¸°ë³¸ì ì¸ í”ë“¤ë¦¼ íš¨ê³¼ */
  animation: glitchy 0.3s steps(2, end) 1;
  /* fadeOut íš¨ê³¼ëŠ” í•„ìš”ì— ë”°ë¼ ë”°ë¡œ ì¶”ê°€í•  ìˆ˜ ìžˆìŒ */
  /* animation: glitchy 0.3s steps(2, end), fadeOut 0.5s ease-in 0.3s forwards; */
