.glitch-out::before,
.glitch-out::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: inherit;
  /* í™”ë©´ ê¹¨ì§ íš¨ê³¼ë¥¼ ìœ„í•´ mix-blend-modeì™€ opacity í™œìš© */
  mix-blend-mode: multiply;
  opacity: 0.8;
  pointer-events: none;
}

/* ìœ„ìª½ íŒŒíŽ¸ íš¨ê³¼: clip-pathë¡œ ìœ„ìª½ ì ˆë°˜ ì˜ì—­ë§Œ ë³´ìž„ */
.glitch-out::before {
  animation: glitchTop 0.3s infinite steps(2, end);
  clip-path: inset(0 0 55% 0);
  filter: hue-rotate(50deg) contrast(150%);
}

/* ì•„ëž˜ìª½ íŒŒíŽ¸ íš¨ê³¼: clip-pathë¡œ ì•„ëž˜ìª½ ì ˆë°˜ ì˜ì—­ë§Œ ë³´ìž„ */
.glitch-out::after {
  animation: glitchBottom 0.3s infinite steps(2, end);
  clip-path: inset(45% 0 0 0);
  filter: hue-rotate(-50deg) brightness(120%);
}

/* ë©”ì¸ glitch íš¨ê³¼: ê¸°ì¡´ ìš”ì†Œì— ì ìš© */
@keyframes glitchy {
  0% {
    transform: translate(0, 0) skew(0deg);
  }
  10% {
    transform: translate(-8px, 4px) skew(8deg);
  }
  20% {
    transform: translate(6px, -4px) skew(-6deg);
  }
  30% {
    transform: translate(-10px, 2px) skew(10deg);
  }
  40% {
    transform: translate(8px, -2px) skew(-8deg);
  }
  50% {
    transform: translate(-6px, 4px) skew(6deg);
  }
  60% {
    transform: translate(4px, -4px) skew(-4deg);
  }
  70% {
    transform: translate(-8px, 2px) skew(8deg);
  }
  80% {
    transform: translate(6px, -2px) skew(-6deg);
  }
  90% {
    transform: translate(-4px, 4px) skew(4deg);
  }
  100% {
    transform: translate(0, 0) skew(0deg);
  }
}

/* ìƒë‹¨ íŒŒíŽ¸ ì• ë‹ˆë©”ì´ì…˜ */
@keyframes glitchTop {
  0% {
    transform: translate(0, 0);
  }
  50% {
    transform: translate(-12px, -6px);
  }
  100% {
    transform: translate(12px, 6px);
  }
}

/* í•˜ë‹¨ íŒŒíŽ¸ ì• ë‹ˆë©”ì´ì…˜ */
@keyframes glitchBottom {
  0% {
    transform: translate(0, 0);
  }
  50% {
    transform: translate(12px, 6px);
  }
  100% {
    transform: translate(-12px, -6px);
  }
}
